.description-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    width: calc(290px + (264 + 264 * 0.7) * ((100vw - 430px) / $hd-plus-width));
    min-height: 200px;

    padding: 16px;
    padding-right: 24px;

    box-sizing: border-box;
    border-radius: 10px;
    background-color: $white;

    &__title {
        margin-bottom: 25px;
    }

    &__more-link {
        display: flex;
        align-items: center;

        color: #3075FC;
        margin-top: 20px;

        &::after {
            content: "";

            display: inline-block;

            width: 14px;
            height: 14px;

            margin-left: 8px;
            margin-top: 3px;

            background: center / contain url("../image/arrow--blue.svg") no-repeat;
        }
    }
}



@media (min-width: $tablet-breakpoint) {
    .description-card {
        width: calc(290px + 264 * (100vw / $hd-plus-width));
    }
}

@media (min-width: $wide-breakpoint) {
    .description-card {
        padding: 30px;
    }
}